  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas: "Photo-basket Title-basket Coast-basket" "Photo-basket Amount Delete-basket";
  margin-top: 33px;
  border-bottom: 2px solid #F9FAEA;
}

.Photo-basket {
  grid-area: Photo-basket;
  background: #E5E5E5;
  width: 100%;
  max-width: 55px;
  border-radius: 8px;
  height: 70px;
  margin-right: 25px;
}

h6 .Title-basket {
  grid-area: Title-basket;
  color: #3C3C3A;
}

.Amount {
  grid-area: Amount;
  width: 111px;
  margin-bottom: 8px;
}

.Coast-basket {
  grid-area: Coast-basket;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.Coast-basket .coast-basket-item.coast-basket-item--total {
  color: #3C3C3A;
}
.Coast-basket .coast-basket-item.coast-basket-item--free {
  font-size: 13px;
}

.Delete-basket {
  grid-area: Delete-basket;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.Delete-basket .icon-delete {
  mask-image: url(/src/assets/static/img/icon-delete.svg);
  mask-position: center center;
  mask-repeat: no-repeat;
  mask-size: contain;
  background: #a3ac9a;
  width: 16px;
  height: 16px;
  margin-left: 10px;
  margin-right: 7px;
  z-index: 5;
}

.Amount .quantity-right {
  padding: 9px 30px;
}
.Amount .quantity-right > input {
  width: 52px;
  height: 22px;
  border: none;
  outline: none;
  text-align: center;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 122%;
}